 * {
     margin: 0;
     padding: 0;
 }
 
 .container {
     background-color: wheat;
     display: flex;
     justify-content: center;
     align-items: center;
 }
 
 .app-wrapper {
     background-color: teal;
     min-width: 450px;
     min-height: 650px;
     padding: 30px;
     box-sizing: border-box;
     border-radius: 10px;
     /* box-shadow: 0 0 0 1px wheat; */
     margin-bottom: 10px;
 }
 
 .header h1 {
     color: wheat;
     text-align: center;
     margin: 30px 0;
 }
 
 .task-input {
     outline: none;
     width: 260px;
     padding: 15px;
     margin-right: 25px;
     font-size: 20px;
     background-color: wheat;
     border: 1px solid #c89666;
     border-radius: 10px;
 }
 
 .button-add {
     width: 70px;
     padding: 15px 15px;
     font-size: 20px;
     border-radius: 10px;
     border: 0;
     background-color: wheat;
     cursor: pointer;
 }
 
 .list-item {
     display: flex;
     margin: 20px 0;
     border: 1px solid #ccc;
     border-radius: 10px;
     padding: 10px;
     max-height: 30px;
 }
 
 .button-complete,
 .button-delete,
 .button-edit {
     border: none;
     outline: none;
     cursor: pointer;
     background-color: transparent;
     font-size: 28px;
     border-radius: 50%;
 }
 
 .button-complete {
     color: #ff6c6c;
     margin-right: 10px;
 }
 
 .button-edit {
     color: #e2d029;
     margin-right: 10px;
 }
 
 .button-delete {
     color: lightseagreen;
 }
 
 .complete {
     text-decoration-style: solid;
     text-decoration-line: line-through;
     text-decoration-color: aqua;
     opacity: 0.6;
 }
 
 .list {
     width: 220px;
     background-color: #12343b;
     border: none;
     color: #ccc;
     font-size: 20px;
     padding-left: 10px;
     margin-right: 15px;
 }
 
 input:focus,
 button:focus {
     outline: none;
 }